.Sidebar {
    width: 80px;
    background-color: rgba(32, 32, 35, 1);
    height: 100%;
    border-right: 1px solid rgba(189, 189, 192, 0.1);
    transition: width 0.3s;
    overflow-y: scroll;
}

.Sidebar::-webkit-scrollbar {
  width: 0px;
}

  /* not small devices  */
  @media (min-width: 576px) {
    .Sidebar {
      width: 320px;
    }
  }